* {
  box-sizing       : border-box;
  /* font-size     : 18px; */
  /* font-family   : 'Poppins', sans-serif; */
  /* font-family   : Verdana, Geneva, Tahoma, sans-serif; */
  /* font-family   : Roboto; */
  /* font-family   : Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif; */
  font-family      : Arial, Helvetica, sans-serif;
  font-family      : serif;
  font-family      : sans-serif;
}

.dashboard {
  min-height: 100vh;
}

.findResults {
  display: grid;
}

.watermark {
  position : fixed;
  bottom   : 10px;
  right    : 10px;
  opacity  : 0.5;
  z-index  : 99;
  color    : grey;
  font-size: 10px;
}

.hideWatermark,
.hideAppTitle {
  display: none;
}

/* .addToHome {
  position: absolute;
  top     : 1px;
  left    : 1px;
  display : none;
} */

.dataInputs {
  height         : 75vh;
  display        : flex;
  flex-direction : column;
  justify-content: space-evenly;
  position       : relative;
}

.appTitle {
  position         : absolute;
  top              : 15px;
  left             : 50%;
  transform        : translateX(-50%);
  /* color         : rgba(200, 200, 200, 0.9); */
  color            : rgba(100, 100, 100, 0.9);
  letter-spacing   : 2px;
  font-weight      : bold;
  font-size        : 20px;
}